import React from 'react';
import Pic from '../../images/pic.jpg';
import {GiCancel} from 'react-icons/gi';
import axios from 'axios';
import {useSelector} from 'react-redux';
import {Link} from "react-router-dom"

export default function ActiveStateView(props) {
 
    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const disableState = () => {
        // setLoading(true);
        axios
            .get(`https://server.wakameals.validprofits.xyz/api/admin/state/disable/${props.data.slug}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                props.loadStateData();
            })
            .catch(error => {
                console.log(error);
            });
    };

    return (
        <tr key={props.data.id}>
          
            <td>{props.data.name}</td>
           
            <td className="text-right">
                <div class="btn-group btn-group-sm" role="group" aria-label="...">
                    <button
                        type="button"
                        className="btn btn-danger"
                        data-toggle="tooltip"
                        data-placement="top"
                        onClick={() => disableState()}
                        title="Block State">
                        <GiCancel />
                    </button>
                    <Link
                        style={{color: 'inherit', textDecoration: 'inherit'}}
                        to={{
                            pathname: `/active-lga-list/${props.data.id}`,
                            state: props.data.id
                        }}>
                        <button
                            type="button"
                            className="btn btn-success btn-sm"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="View LGA">
                            active lga
                        </button>
                    </Link>
                    <Link
                        style={{color: 'inherit', textDecoration: 'inherit'}}
                        to={{
                            pathname: `/blocked-lga-list/${props.data.id}`,
                            state: props.data.id
                        }}>
                        <button
                            type="button"
                            className="btn btn-dark btn-sm"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="View LGA">
                             blocked lga
                        </button>
                    </Link>
                </div>
            </td>

        </tr>
    );
}
