import React from 'react';
import OrderListNav from '../Navigation/OrderListNav';
import DeliveryManListNav from '../Navigation/DeliveryManListNav';
import UserListNav from "../Navigation/UserListNav"
import MealListNav from '../Navigation/MealListNav'
import CategoryNav from "../Navigation/CategoryNav"
import SubCategoryNav from "../Navigation/SubCategoryNav"
import  StateListNav from "../Navigation/StateListNav"
import pic from '../../images/wmlogo.jpg';
import {Link} from "react-router-dom"

export default function Dashboard(props) {
    return (
        <div className="user-dashboard mt-5">
            <div className="container">
            <div className="text-center">
               
                <h3 style={{color: 'black', fontWeight: 'bold', marginTop: '15%'}}>Admin</h3>
            </div>
                <div className="row">
                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                        <div
                            class="card shadow border-0 card-hover "
                            data-toggle="modal"
                            data-target="#orderListNavModal">
                            <div class="card-body text-center">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                    Order List
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                        <div class="card shadow border-0 card-hover"  data-toggle="modal"
                            data-target="#mealListNavModal">
                            <div class="card-body text-center">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                    Meal
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                        <div class="card shadow border-0 card-hover"  data-toggle="modal"
                            data-target="#userListNavModal">
                            <div class="card-body text-center">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                    User List
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                        <div class="card shadow border-0 card-hover">
                            <div class="card-body text-center"
                            data-toggle="modal"
                            data-target="#stateListNavModal">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                    State / Location List
                                </p>
                            </div>
                        </div>
                    </div>
                 
                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                        <div
                            class="card shadow border-0 card-hover"
                            data-toggle="modal"
                            data-target="#deliveryManListNavModal">
                            <div class="card-body text-center">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                    Delivery-Man List
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                        <div class="card shadow border-0 card-hover"
                          data-toggle="modal"
                          data-target="#categoryNav">
                            <div class="card-body text-center">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                 Category
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                        <div class="card shadow border-0 card-hover"
                          data-toggle="modal"
                          data-target="#subCategoryNav">
                            <div class="card-body text-center">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                Sub Category
                                </p>
                            </div>
                        </div>
                    </div>

                    

                    <div className="col-lg-3 col-md-6 col-12 mb-3 ">
                    <Link to="/change-password" style={{color: 'inherit', textDecoration: 'inherit'}}>
                        <div class="card shadow border-0 card-hover" >
                            <div class="card-body text-center">
                                <p class="card-text" style={{fontWeight: 'bold', fontSize: '0.7em'}}>
                                    Change Password
                                </p>
                            </div>
                        </div>
                        </Link>
                    </div>
                </div>
            </div>

            {/* ========ORDER LIST NAV ======= */}
            <OrderListNav {...props} />
            {/* ======DELIVERY MAN LIST NAV ======== */}
            < DeliveryManListNav {...props} />
            {/* =====USER LIST NAV ====== */}
            < UserListNav {...props} />
            {/* ========= MEALS NAV ========== */}
            <MealListNav {...props} />
            {/* =======CATEGORY NAV ====== */}
            <CategoryNav {...props}/>
            {/* ====== SUB CATEGORY NAV ===== */}
            <SubCategoryNav {...props}/>
            {/* ====== STATE NAV ===== */}
            <StateListNav {...props}/>
        </div>
    );
}
