import {useState,useEffect} from 'react';
import BlockedLGAView from '../ReuseableCompononts/BlockedLGAView';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';
import {useLocation} from 'react-router-dom';

export default function ActiveLGAList(props) {
    const [stateData,setStateData] = useState([]);
    const [loading, setLoading] = useState();


    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    let location = useLocation();

    const loadStateData = () => {
        setLoading(true);
        axios
            .get(`https://server.wakameals.validprofits.xyz/api/admin/lga/list/disabled/${location.state}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setStateData(res.data.lgas);
                setLoading(false);
               
            })
            .catch(error => {
                console.log(error);
                setLoading(false);
            });
    };

    useEffect(() => {
        loadStateData();
    }, []);
    return (
        <div>
            {loading && (
                <div style={{height: '100vh', width: '100%'}}>
                    <Preloader />
                </div>
            )}
            {stateData.length < 1 ? (
                <div style={{marginTop: '10%'}}>
                    <p className="text-center">
                        <b>No LGA Found</b>
                    </p>
                </div>
            ) : (
            <div class="table-responsive">
                <table class="table">
                    <thead class="thead-light" style={{fontSize: '0.7em', width: '50px'}}>
                        <tr>
                            <th>Name</th> 
                            <th className="text-right">Action</th>
                        </tr>
                    </thead>
                    <tbody style={{fontSize: '0.7em', width: '50px'}}>
                    {stateData.map(data => <BlockedLGAView data={data} {...props} loadStateData={loadStateData} />)}
                    </tbody>
                </table>
            </div>
            )}
        </div>
    );
}
